import * as diff from 'deep-diff';

export default class Utility {

  public static compare(base: any, data: any): any {

    const difference = diff(base, data);
    const final_diff = {};

    if (!difference) {
      return final_diff;
    }

    for (const item of difference) {

      if (item.kind === 'A') {
        final_diff[item.path[0]] = data[item.path[0]];
      } else if (item.kind === 'E') {

        let reference = final_diff;
        for (let index = 0; index < item.path.length - 1; index++) {

          if (!reference[item.path[index]]) {
            reference[item.path[index]] = {};
          }
          reference = reference[item.path[index]];
        }
        reference[item.path[item.path.length - 1]] = item.rhs;

      } else if (item.kind !== 'D') {
        final_diff[item.path[0]] = item.rhs;
      } else {
        final_diff[item.path[0]] = undefined;
      }
    }

    return final_diff;
  }
}
